import Layer from './layer'
import config from '../../config'

export default class Tilemap extends PIXI.Container {
    layers = {}
    mapData = null

    constructor(map, tilesheet) {
        super()
        this.mapData = map
        map.layers.forEach((layer, i) => {
            if (layer.type !== 'tilelayer') return
            this.layers[layer.name] = new Layer(
                layer,
                tilesheet,
                this.mapData.tilewidth,
                this.mapData.tileheight,
                map.tilesets[0]
            )
        })
        this.addChild(...Object.values(this.layers))
    }
}