import {monitor} from '../core'


class Player extends PIXI.Container {
    walked = false
    jumped = false
    back = false
    speed = {x: 5, y: 10}


    constructor() {
        super()
        this.action = {
            walk: PIXI.extras.AnimatedSprite.fromFrames([
                'player.walk.1.png',
                'player.walk.2.png',
                'player.walk.3.png'
            ]),

            stand: PIXI.extras.AnimatedSprite.fromFrame('player.stand.png'),

            jump: PIXI.extras.AnimatedSprite.fromFrame('player.up.1.png')

        }

        this.action.walk.visible =
        this.action.jump.visible = false
        this.action.walk.animationSpeed = .18
        this.addChild(...Object.values(this.action))
        this.setBody()
        this.listen()
    }


    setBody() {
        this.pivot.set(this.width / 2, this.height / 2)
        this
            .addBody({
                linearDamping: .3,
                gravityScale: 2
            })
            .addCircle({
                r: this.width * .5,
                center: {x: 0, y: 4},
                def: {
                    restitution: 0
                }
            })
            .addCircle({
                r: 7.624,
                center: {x: 0, y: 24},
                def: {
                    isSensor: true
                }
            })
            .setFixedRotation(true)

        this.body.collidable = true
    }

    walk(back=false) {
        if (this.action.walk.visible && back === this.back) return
        this.back = back
        this.action.walk.textures.forEach(texture => {
            texture.rotate = back ? 12 : 0
        })
        this.hideActions()
        this.action.walk.play()
        this.action.walk.visible = true
    }

    stand() {
        if (this.action.stand.visible) return
        this.hideActions()
        this.action.stand.visible = true
    }

    jump() {
        if (this.action.jump.visible) return
        this.hideActions()
        this.action.jump.visible = true

    }

    hideActions() {
        Object.values(this.action).forEach(action => action.visible = false)
    }

    listen() {
        monitor
            .on('player:left', () => {
                this.walked = true
                this.back = true
            })
            .on('player:right', () => {
                this.walked = true
                this.back = false
            })
            .on('player:jump', () => {
                if (this.jumped) return
                this.jumped = true
                this.body.applyLinearImpulse(
                    {x: 0, y: -30},
                    this.body.getPosition(),
                    true
                )
            })
            .on('player:stand', () => {
                this.walked = false
                this.stand()
            })

        this
            .on('begin-contact', (self, other, contact) => {
                if (self.collideFixture.isSensor()) {
                    this.jumped = false
                }
            })
    }

    update() {
        const v = this.body.getLinearVelocity()
        if (this.walked) {
            v.x = this.back ? this.speed.x * -1 : this.speed.x
            this.walk(this.back)
        } else {
            v.x = 0
        }

        this.body.setLinearVelocity(v)
    }
}

export default {
    create() {
        return new Player()
    }
}