import core from '../index'

export default class TileLayer extends PIXI.Container {
    name = ''
    size = {width: 0, height: 0}
    tilesheet = null
    visible = true
    square = 32

    constructor(data, tilesheet, tileWidth, tileHeight, tilesets) {
        super()
        this.size.width = data.width
        this.size.height = data.height
        this.tileHeight = tileHeight
        this.tileWidth = tileWidth
        this.tilesheet = tilesheet
        this.square = tileWidth
        this.name = data.name
        this.visible = data.visible
        this.data = data.data
        this.tilesets = tilesets
        this.fill()
    }

    fill() {
        const rect = new PIXI.Rectangle(0, 0, this.square, this.square)
        for (let i = 0, sn = 0; i < this.size.height; i++) {
            for (let j = 0; j < this.size.width; j++) {
                sn = this.data[i * this.size.width + j]
                if (!sn) continue
                sn--
                rect.x = (sn % this.tilesets.columns) * this.square
                rect.y = (~~(sn / this.tilesets.columns)) * this.square
                const tile = new PIXI.Sprite(new PIXI.Texture(this.tilesheet, rect))
                tile.position.set(j * this.square, i * this.square)
                tile.anchor.set(.5)
                tile.sn = sn
                this.addChild(tile)
            }
        }
    }

    setCollisionByProperty(key) {
        const properties = this.tilesets.tileproperties

        /* one body multiple fixtures */
        this.addBody().setStatic()
        this.children.forEach(tile => {
            const property = properties[tile.sn]
            if (property && property[key]) {
                this.body.addBox({
                    width: this.square,
                    height: this.square,
                    center: tile.position,
                    def: {
                        friction: 0
                    }
                })
            }
        })
    }

}