import core, {monitor, Tilemap, camera} from '../core'
import config from '../config'
import {gamepad, role} from '../components'

core.loader.baseUrl = config.cdn

export default {
    v: {x: 0, y: 0},
    container: new PIXI.Container(),

    load() {
        Promise.all([
            new Promise(resolve => {
                core.loader
                    .add('player', 'static/textures/player.json?' + stamp)
                    .add('enemy', 'static/textures/enemy.json?' + stamp)
                    .add('map', 'static/maps/1.json?' + stamp)
                    .add('bkg', 'static/textures/bkg/bkg.1.png?' + stamp)
                    .add('hill', 'static/textures/bkg/hill.1.png?' + stamp)
                    .add('tile', 'static/textures/bkg/tile.1.png?' + stamp)
                    .load(resolve)
            }),

            new Promise(resolve => {
                this.tilesheet =
                    PIXI.BaseTexture.from(`${config.cdn}/static/textures/tilesheet.png?${stamp}`)
                this.tilesheet.on('loaded', resolve)
            })
        ]).then(this.create.bind(this))
    },

    create() {
        this.camera = camera.create()
        this.bkg = this.addBkg()
        this.player = this.addPlayer()
        this.gamepad = gamepad.create()
        this.map = this.addMap()

        this.camera.addChild(this.map, this.player)
        this.camera.follow(this.player)

        config.debug && this.camera.addChild(core.scanner)

        this.container.addChild(this.bkg, this.camera, this.gamepad)
        this.listen()
        core.stage.addChild(this.container)
        core.ticker.add(this.update.bind(this))
    },


    addBkg() {
        const
            bkg = PIXI.extras.TilingSprite.from('bkg', config.screen.width, config.screen.height),
            tile = PIXI.extras.TilingSprite.from('tile', config.screen.width, config.screen.height),
            hill = PIXI.Sprite.from('hill')

        hill.scale.set(Math.max(
            config.screen.width / hill.width,
            config.screen.height / hill.height
        ))
        hill.anchor.set(.5)
        hill.position.set(config.screen.width * .5, config.screen.height * .5)
        bkg.addChild(tile, hill)
        bkg.scale.set(1 / config.zoom)
        return bkg
    },


    addPlayer() {
        const player = role.create()
        player.body.syncPosition(100, 0)
        return player
    },

    addMap() {
        const map = new Tilemap(
            core.loader.resources.map.data,
            this.tilesheet
        )

        map.layers.ground.setCollisionByProperty('collides')

        return map
    },

    listen() {

    },

    update(t) {
        this.player.update()
        this.camera.update(t)
    }
}