import config from '../config'
import {world, ptm} from './physics'
import planck from 'planck-js'
import * as PIXI from 'pixi.js'

let pixelRatio, windowHeight, windowWidth

if (typeof wx !== 'undefined') {
    ({pixelRatio, windowHeight, windowWidth} = wx.getSystemInfoSync())

    PIXI.utils.isWebGLSupported = () => {
        return canvas.getContext('webgl')
    }

    PIXI.interaction.InteractionManager.prototype.mapPositionToPoint = (point, x, y) => {
        point.x = x * pixelRatio
        point.y = y * pixelRatio
    }
} else {
    pixelRatio = window.devicePixelRatio
    windowWidth = window.innerWidth
    windowHeight = window.innerHeight
    window.canvas = document.querySelector('canvas')

    if (window.orientation === 0) {
        windowWidth = window.innerHeight
        windowHeight = window.innerWidth

        canvas.style.width = `${windowWidth}px`
        canvas.style.height = `${windowHeight}px`
        canvas.style.top = `${(innerHeight - windowHeight) * .5}px`
        canvas.style.left = `${(innerWidth - windowWidth) * .5}px`
    }

    PIXI.interaction.InteractionManager.prototype.mapPositionToPoint = function(point, x, y) {
        let rect

        // IE 11 fix
        if (!this.interactionDOMElement.parentElement) {
            rect = {x: 0, y: 0, width: 0, height: 0}
        } else {
            rect = this.interactionDOMElement.getBoundingClientRect()
        }

        const resolutionMultiplier = navigator.isCocoonJS ? this.resolution : (1.0 / this.resolution)

        /*
        * : 
        */
        if (window.orientation === 0) {
            point.x = (y - rect.top) * (this.interactionDOMElement.width / rect.height) * resolutionMultiplier
            point.y = (1 - (x - rect.left) / rect.width) * this.interactionDOMElement.height * resolutionMultiplier
        } else {
            point.x = ((x - rect.left) * (this.interactionDOMElement.width / rect.width)) * resolutionMultiplier
            point.y = ((y - rect.top) * (this.interactionDOMElement.height / rect.height)) * resolutionMultiplier
        }
    }
}

config.screen.width = windowWidth * pixelRatio
config.screen.height = windowHeight * pixelRatio
config.zoom = Math.min(config.screen.width / config.width, config.screen.height / config.height)

const app = new PIXI.Application({
    width: config.screen.width,
    height: config.screen.height,
    view: canvas,
    backgroundColor: config.bgColor,
    antialias: true
})

app.stage.scale.set(config.zoom)



export default app
export {default as sound} from './sound'
export {default as Layout} from './layout'
export {default as camera} from './camera'
export {default as Tilemap} from './tilemap/index'
export const monitor = new PIXI.utils.EventEmitter()

if (config.debug) {
    // 
    const
        aabb = planck.AABB(),
        graphics = new PIXI.Graphics()

    app.scanner = graphics

    app.ticker.add(() => {
        graphics.clear()
        graphics.lineStyle(1, 0x00bcd4)

        for (let body = world.getBodyList(); body; body = body.getNext()) {
            for (let fixture = body.getFixtureList(), xf = body.getTransform();
                fixture; fixture = fixture.getNext()) {
                const shape = fixture.getShape()

                if (shape.m_type === planck.Circle.TYPE) {
                    const c = planck.Vec2.add(xf.p, planck.Rot.mul(xf.q, shape.m_p))
                    graphics.drawCircle(
                        c.x * ptm,
                        c.y * ptm,
                        shape.m_radius * ptm
                    )
                } else if (shape.m_type === planck.Polygon.TYPE) {
                    graphics.drawPolygon(shape.m_vertices.map(point => {
                        point = planck.Transform.mul(xf, point)
                        return new PIXI.Point(point.x * ptm, point.y * ptm)
                    }))
                    graphics.closePath()
                } else {
                    shape.computeAABB(aabb, xf)
                    aabb.lowerBound.x *= ptm
                    aabb.lowerBound.y *= ptm
                    aabb.upperBound.x *= ptm
                    aabb.upperBound.y *= ptm
                    graphics.drawRect(
                        aabb.lowerBound.x,
                        aabb.lowerBound.y,
                        aabb.upperBound.x - aabb.lowerBound.x,
                        aabb.upperBound.y - aabb.lowerBound.y
                    )
                }
            }
        }
    })

    /*
    * 
    */
    PIXI.DisplayObject.prototype.drag = function() {
        let down = false,
            delta

        this.interactive = true

        this
            .on('pointerdown', event => {
                down = true
                delta = event.data.getLocalPosition(this)
                event.stopPropagation()
            })
            .on('pointerup', () => {
                down = false
                console.log(`${~~this.x}, ${~~this.y}`)
            })
            .on('mouseup', event => {
                console.log('up')
            })
            .on('pointermove', event => {
                if (down) {
                    const
                        point = event.data.getLocalPosition(this),
                        x = point.x - delta.x,
                        y = point.y - delta.y,
                        s = Math.sqrt(x ** 2 + y ** 2),
                        theta = Math.atan2(y, x)

                    this.x += Math.cos(theta + this.rotation) * s
                    this.y += Math.sin(theta + this.rotation) * s
                }
            })

        return this
    }
}
